@import "variables.scss";

*,
*::before,
*::after {
	box-sizing: border-box;
}

a {
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

button {
	background-color: transparent;
	color: inherit;
	border-width: 0;
	padding: 0;
	cursor: pointer;
}

figure {
	margin: 0;
}

input::-moz-focus-inner {
	border: 0;
	padding: 0;
	margin: 0;
}

ul,
ol,
dd {
	margin: 0;
	padding: 0;
	list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-size: inherit;
	font-weight: inherit;
}

p {
	margin: 0;
}

cite {
	font-style: normal;
}

fieldset {
	border-width: 0;
	padding: 0;
	margin: 0;
}

body {
	font-family: $main_font;
	font-size: 16px;
	color: #5e5e5e;
	background-color: #fdfaf3;
	background-image: url(../img/bg.jpg);
	background-position: center;
	background-repeat: repeat-y;
}

body:not(.accesibility) a,
body:not(.accesibility) input,
body:not(.accesibility) button {
	outline: none !important;
}

img {
	max-width: 100%;
}

/*----Hamburger----*/
$s: 3px;

.burger-wrapper {
	cursor: pointer;
	margin: 0;
	width: $s*10;
	height: $s*8;

	.hamburger {
		display: inline-block;
		background: #fff;
		width: $s*10;
		height: $s;
		position: relative;
		transition: background 10ms 300ms ease;
		transform: translateY($s*4);

		&:before,
		&:after {
			transition: top 300ms 350ms ease,
				transform 300ms 50ms ease;
			position: absolute;
			background: #fff;
			;
			width: $s*10;
			height: $s;
			content: '';
		}

		&:before {
			top: $s*-3;
		}

		&:after {
			top: $s*3;
		}
	}
}

.menu-trigger:checked {
	~.burger-wrapper {
		.hamburger {
			background: transparent;

			&:after,
			&:before {
				transition: top 300ms 50ms ease,
					transform 300ms 350ms ease;
				top: 0;
			}

			&:before {
				transform: rotate(45deg);
			}

			&:after {
				transform: rotate(-45deg);
			}
		}
	}
}

.hidden {
	display: none;
}

/*--------*/

.tabs__content {
	display: none;
	/* по умолчанию прячем все блоки */
}

.tabs__content.active {
	display: block;
	/* по умолчанию показываем нужный блок */
}

@media (min-width: 1200px) {

	.container,
	.container-lg,
	.container-md,
	.container-sm,
	.container-xl {
		max-width: 1200px !important;
	}
}

a {
	transition: $trans;
}

.button {
	font-family: $main_font;
	color: #fff;
	font-size: 15px;
	letter-spacing: 1.5px;
	border: 2px solid #fff;
	display: inline-block;
	transition: $trans;

	&:hover {
		border-color: $accent_color;
		background-color: $accent_color;
		color: #fff;
	}
}

.top-section {
	position: relative;

	&::before {
		position: absolute;
		content: '';
		width: 50%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: #112537;
		opacity: 0.97;
	}

	header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 85px;
	}
}

.main-slider_slide {
	background-size: cover;
	background-repeat: no-repeat;

	.slide-content {
		z-index: 1;

		&::before {
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: #112537;
			opacity: 0.97;
			z-index: -1;
		}

		position: relative;
		padding: 150px 94px;
		color: #fff;

		h1 {
			font-family: $titles_font;
			font-size: 60px;
			font-weight: 700;
			letter-spacing: 1.5px;
			line-height: 1;
			margin: 12px 0 22px;
		}

		h2 {
			font-family: $main_font;
			font-size: 14px;
			font-weight: 300;
			line-height: 1.5;
			letter-spacing: 0.7px;
			margin-bottom: 30px;
		}

		.button {
			padding: 20px 30px;

			img {
				display: inline-block;
				margin-left: 18px;
			}
		}
	}

}


.slick-dots {
	position: absolute;
	right: 15px;
	padding: 32px 0;

	li {
		margin-right: 12px;
		display: inline-block;

		&:last-of-type {
			margin-right: 0;
		}

		&.slick-active {
			button {
				opacity: 1;
			}
		}

		button {
			border: 3px solid #1a444a;
			font-size: 0;
			width: 26px;
			opacity: 0.2;
		}
	}
}

.header_logo {
	justify-content: center;
}

.header_phone {
	float: right;
	font-family: sans-serif;
	font-weight: 700;
	color: #252525;
	justify-content: flex-end;
	transition: $trans;

	&:hover {
		color: $accent_color;
	}

	i {
		padding-right: 5px;
	}
}

.socials_block {
	color: #fff;
	display: flex;
	align-items: center;
	height: 90px;

	a {
		&:hover {
			color: $accent_color;
		}

		i {
			font-size: 14px;
			margin-right: 36px;
			transition: $trans;
		}

		&:last-of-type {
			i {
				margin-right: 0;
			}
		}
	}
}

#features {
	padding: 80px 0;
}

.feature_item {
