	}

	.feature_number {
		font-family: $titles_font;
		font-size: 86px;
		font-weight: 700;
		color: rgba(254, 160, 82, 0.26);
		letter-spacing: 0.86px;
		line-height: 1.4;
	}

	.feature_title {
		font-family: $main_font;
		font-size: 22px;
		font-weight: 700;
		color: #1a292f;
		margin-bottom: 14px;
	}

	.feature_dscr {
		font-family: $main_font;
		font-weight: 300;
		line-height: 1.61;
		letter-spacing: 0.78px;
		color: #838383;
	}
}

.sec_title {
	font-family: $titles_font;
	font-size: 45px;
	font-weight: 700;
	letter-spacing: 0.45px;
	color: $main_color;
	margin-bottom: 50px;
}

.filter {
	a {
		font-family: $main_font;
		font-size: 18px;
		font-weight: 700;
		color: #807f7f;
		transition: $trans;
		display: inline-block;
		margin-right: 55px;
		padding-bottom: 10px;

		&.active {
			color: $main_color;
			border-bottom: 1px solid $main_color;
		}

		&:hover {
			color: $main_color;
			border-bottom: 1px solid $main_color;
		}
	}
}

.portfolio_carousel {
	margin-top: 47px;
	margin-bottom: 50px;

	.slide {
		color: #fff;
		position: relative;
		transition: all 0.25s ease;
		height: 551px;
		background-size: cover;
		background-repeat: no-repeat;

		.slide_title {
			position: relative;
			z-index: 1;
			font-weight: 700;
			letter-spacing: 0.78px;

		}

		.slide_cat {
			font-family: sans-serif;
			font-size: 14px;
			position: absolute;
			transform: rotate(-90deg);
			bottom: 20px;
			transform-origin: top left;
		}

		&:focus {
			outline: none;
		}

		.mask {
			transition: $trans;
			padding: 32px 26px;
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: #112537dd;
		}

		.slide_link {
			position: absolute;
			left: 50%;
			top: 50%;
			// transform: translate (-50%, -50%);
			transform: translate(-50%, -50%);
			opacity: 0;
			width: 84px;
			height: 84px;
			border-radius: 50%;

			&::before {
				position: absolute;
				content: '';
				width: 26px;
				height: 2px;
				left: 29px;
				top: 41px;
				background-color: #fff;
			}

			&::after {
				position: absolute;
				content: '';
				width: 26px;
				height: 2px;
				left: 29px;
				top: 41px;
				background-color: #fff;
				transform: rotate(-90deg);
			}
		}

		&:hover {
			.mask {
				opacity: 0;
			}

			.slide_link {
				opacity: 1;
				background-color: #febc80;

				&:hover {
					background-color: $accent_color;
				}
			}
		}
	}

	.slick-list {
		max-height: 551px;
	}

	.slick-arrow {
		position: absolute;
		top: 50%;
		z-index: 1;
		padding: 10px 0;
		margin-top: -10px;

		svg path {
			fill: #fdfaf3
		}

		&:hover {
			svg path {
				fill: $accent_color;
			}
		}
	}

	.slick-prev {
		left: 33px;
	}

	.slick-next {
		right: 33px;
	}
}

.tabs {
	display: flex;
}

#services {
	padding-top: 70px;
	padding-bottom: 80px;
}

