		opacity: 0.7;
		font-size: 14px;
		font-weight: 300;
		letter-spacing: 0.7px;
		text-align: left;
		color: #fff
	}
}

.testimonials .testimonial-slider_wrap {
	padding-left: 0;
	padding-right: 0;
}

.testimonials .testimonial-slider_title {
	max-width: 1200px;
	margin: 0 auto;
}

.testimonials {
	.testimonial-slider {
		margin: 0 auto;
		max-width: 1337px;

		.slick-dots {
			right: 50%;
			bottom: -115px;
			transform: translate(50%);
		}

		.testimonial-item {
			outline: none;
			display: flex;
			margin: 0 -27px 0 -10px;
			align-items: center;

			.testimonial-item_content {
				transition: $trans;
				visibility: hidden;
				opacity: 0;
				flex-basis: 652px;
				max-width: 652px;
				background: #fff;
				padding: 54px 120px 50px 71px;

				.testimonial_author {
					display: block;
					font-size: 22px;
					font-weight: 700;
					line-height: 1.36;
					color: #313131;
					margin-bottom: 12px;
				}

				.testimonial_service {
					display: block;
					font-size: 15px;
					font-weight: 700;
					letter-spacing: 1.24px;
					color: #efb172;
					margin-bottom: 28px;
				}

				.testimonial_quote {
					font-size: 16px;
					font-weight: 300;
					line-height: 1.56;
					color: #5e5e5e;
				}
			}
		}
	}

	.testimonial_thumb {
		transition: $trans;
		transform: translateX(0);
		flex-basis: 192px;
		position: relative;
		left: -75px;
		width: 100%;
	}
}


.testimonial-slider .slick-list {
	margin-left: 13px;
}

.testimonials .testimonial-slider .testimonial-item.slick-center .testimonial-item_content {
	visibility: visible;
	opacity: 1;
}

.testimonials .testimonial-slider .testimonial-item.slick-center .testimonial_thumb {
	transform: translateX(0);
}

.testimonials .testimonial-slider .testimonial-item.slick-center+.testimonial-item .testimonial_thumb {
	transform: translateX(-577px);
}

