	}

	.services .tabs__caption li {
		width: 50%;
		max-width: 50%;
		float: left;
		font-size: 22px;
		padding-bottom: 0;
		height: 110px;
	}

	#services {
		padding-bottom: 280px;
	}
	.services .service_content_wrap .service_add_info {
		font-size: 20px;
	}
	.cta {text-align: center;}
	.cta .cta_content .cta_wrap {
		margin: 0 auto;
		.cta_text {
			max-width: 470px;
			margin: 0 auto;
		}
		.cta_form {
			max-width: 470px;
			margin: 0 auto;

		}
}
}



@media screen and (max-width : 768px) {
	.bottom-slider {
		display: none;
