#togglenav:checked ~ nav {
	display: flex;
	transform: translateX(0);
	width: 1170px;
	opacity: 1;


}

header {
	nav {
		opacity: 0;
		transform: translateX(333.3%);
		transition: $trans;
		position: absolute;
    z-index: 999;
		top: 54px;
		left: 15px;
    justify-content: center;
    align-items: center;
		background-color: #00203a;
		height: 588px;
		width: 0;
		text-align: center;
		overflow: hidden;
		ul {
			li {
				a {
					font-size: 24px;
				text-transform: uppercase;
					display: inline-block;
					padding: 10px 0;
					color: rgba(255, 255, 255, 0.502);
					font-weight: 400;
					line-height: 2.1;
					text-align: center;
					&.active, &:hover {
						color: $accent_color;
					}
				}
			}
		}
	}
}

